.Login{
  width: 400px;
  max-width: 90%;
  margin-left: calc(50% - 200px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
  padding: 40px 60px;
  padding-top: 190px;
  margin-top: 80px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}

@media screen and (max-width: 447px){
  .Login{
    width: 90%;
    margin-left: 5%;
    margin-top: 40px;
  }
}

  .Login .Login-header{
    height: 150px;
    background-image: url("/images/bg-login.png");
    width: 100%;
    position: absolute;
    top: 0;
  }

  .Login > div {
    width: 100%;
    text-align: center;
  }

  .Login button, .Login input, .Login a{
    width: 100%;
    margin: 5px 0;
    text-align: center;
    border-radius: 5000px;
    -webkit-box-shadow: 2px 3px 10px 0px rgba(0, 0, 0, 0.2) inset;
            box-shadow: 2px 3px 10px 0px rgba(0, 0, 0, 0.2) inset;
    border: none;
  }

  .Login .btn-success{
    background-color: #71edaf;
    font-weight: bold;
  }

  .Login .forget-password{
    color: #696969;
    font-size: .9em;
  }

  .Login .btn-primary{
    background-color: #8fe4f1;
    font-weight: bold;
  }

  .Login .register-link{
  }



