html{
  height: 100%;
  width: 100%;
  overflow: hidden;
}
body{
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(236,234,234,1)),to(rgba(255,255,255,1)));
  background: -webkit-linear-gradient(top, rgba(236,234,234,1) 0%,rgba(255,255,255,1) 100%);
  background: -o-linear-gradient(top, rgba(236,234,234,1) 0%,rgba(255,255,255,1) 100%);
  background: linear-gradient(to bottom, rgba(236,234,234,1) 0%,rgba(255,255,255,1) 100%);
  font-family: "Ubuntu";
  height: 100%;
  width: 100%;
  overflow: hidden;
}

/* GLOBAL */
.floating-box{
  -webkit-box-shadow: -13px 15px 25px 0px rgba(0, 0, 0, 0.2);
          box-shadow: -13px 15px 25px 0px rgba(0, 0, 0, 0.2);
}

.main-nav{
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: white;
  -webkit-box-shadow: 3px 4px 20px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 3px 4px 20px 0px rgba(0, 0, 0, 0.2);
  color: #979797;

}

  .main-nav .container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .gomycode-title{
    font-size: 1.3em;
    font-family: "Ubuntu-Light";
  }

  .whole-title{

  }

  .responsive-title{
    display: none
  }

  .expand-menu-responsive{
    display: none;
  }

  .main-menu{
    height: 60px;
  }

  .main-menu > ul{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    list-style: none;
    height: 60px;
  }

    .main-menu > ul > li{
      margin: 0 10px;
    }


.main-menu > i {
  display: none;
}

@media screen and (max-width: 447px) {
  .main-nav .container{
    height: auto;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
  }

  .gomycode-title{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
  }

  .whole-title{
    display: none;
  }

  .responsive-title{
    display: block
  }

  .expand-menu-responsive{
    display: block;
  }

  .main-menu{
    position: absolute;
    background-color: white;
    height: 100%;
    top: 0;
    right: 0;
    padding: 40px 70px 40px 20px;
    -webkit-box-shadow: 3px 4px 20px 0px rgba(0, 0, 0, 0.2);
            box-shadow: 3px 4px 20px 0px rgba(0, 0, 0, 0.2);
    z-index: 999999;
    -webkit-transition: right 0.3s;
    -o-transition: right 0.3s;
    transition: right 0.3s;
  }

  .main-menu > i {
    display: block;
    position: absolute;
    right: 20px;
    top: 20px;
  }

  .main-menu > ul{
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    width: 100%;
    padding: 0;
  }

  .main-menu > ul > li{
    margin: 10px;
  }
}



