.VerticalViewLessons{
  display: -ms-flexbox;
  display: flex;
}

  .lesson{
    border: solid 1px;
    padding: 40px 40px 0 40px;
    margin: 10px;
  }

  .lesson h1{
    background: #eee;
  }

  .lessonName{
    border: solid 1px;
    margin: 10px;
    border-radius: 10px;
  }


.HorizontalViewLessons .lesson{
  position: relative;
  padding: 40px;

}

  .HorizontalViewLessons .lesson .left-section{
    position: absolute;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
    width: 40px;
    background-color: #eee;
  }

    .HorizontalViewLessons .lesson .left-section > div{
      -webkit-transform: rotate(-90deg);
          -ms-transform: rotate(-90deg);
              transform: rotate(-90deg)
    }

.SwitchButton{
    display: -ms-flexbox;
    display: flex;
    height: 20px;
    width: 100px;
    border: solid 1px;
    border-radius:  5000px;
    overflow: hidden;
    margin: 10px;
    cursor: pointer;
}

.SwitchButton > div{
    height: 100%;
    width: 50px;
}



